.breadcrum{
    display: flex;
    align-items: center;
    gap:10px;
    color:black;
    font-size:20px;
    font-weight:2000px;
    margin:60px 170px;
    text-transform:capitalize;
}
@media(max-width:1280px){
    .breadcrum{
        margin: 30px 50px;
        font-size:14px;
    }
} 
@media(max-width:1024px){
    .breadcrum{
        margin:30px 30px;
        font-size:13px;
    }
}
@media(max-width:800px)
{
    .breadcrum{
        margin: 30px 10px;
        font-size:12px;
    }
}
@media(max-width:500px)
{
    .breadcrum{
        font-size:10px;
    }
}